import React, {useState, useEffect} from 'react';
import Header from '../header';
import styles from './quiz.module.scss';
import axios from "axios";

function Quiz(props) {

	const [currentQuestion, setCurrentQuestion] = useState(0);
	const [questions, setQuestion] = useState(0);
	const [showScore, setShowScore] = useState(false);
	const [score, setScore] = useState(0);
	const [loading, setLoading] = useState(true);

	const handleAnswerOptionClick = (isCorrect) => {
		if (isCorrect) {
			setScore(score + 1);
		}
		const nextQuestion = currentQuestion + 1;
		if (nextQuestion < questions.length) {
			setCurrentQuestion(nextQuestion);
		} else {
			setShowScore(true);
		}
	};
  function resetQuiz(){
    setCurrentQuestion(0);
    setShowScore(false);
    setScore(0);
  }
  function getQuestion(){
    axios
      .get("https://opentdb.com/api.php?amount=10")
      .then((res) => {
          console.log(res.data.results);
          setQuestion(makeQuestions(res.data.results));
          setLoading(false);
      })
      .catch((err) => {
          console.log(err);
      });
  }
  function makeQuestions(questionSet){
    let temp = [];
    let query = [];
    let answers = [];
    questionSet.map((item, i)=>{
      query = [];
      query['answerOptions'] = [];
      answers = [];
      query['questionText'] = item.question;
      answers['answerText'] = item.correct_answer;
      answers['isCorrect'] = true;
      query['answerOptions'].push(answers);
      item['incorrect_answers'].map((wrong, j)=>{
        answers = [];
        answers['answerText'] = wrong;
        answers['isCorrect'] = false;
        query['answerOptions'].push(answers);
      });
      temp.push(query);
    });
    return temp;
  }
  function shuffleQuery(array) {
    var currentIndex = array.length,  randomIndex;  
    while (0 !== currentIndex) {  
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex--;
      [array[currentIndex], array[randomIndex]] = [array[randomIndex], array[currentIndex]];
    }  
    return array;
  }
  useEffect(()=>{
    getQuestion();
  },[loading]);
	return (
    <>
      <Header number={props.number} />
      <div className={styles['App-container']}>
        {
          !loading? 
          <div className={styles['quiz-container']}>
            {showScore ? (
              <div className={styles['score-section']}>
                <div>
                  You scored {score} out of {questions.length}
                </div>
                <div onClick={resetQuiz} className={styles['reset-btn']}>Retake Quiz</div>
              </div>
            ) : (
              <>
                <div className={styles['question-section']}>
                  <div className={styles['question-count']}>
                    <span>Question {currentQuestion + 1}</span>/{questions.length}
                  </div>
                  <div className={styles['question-text']} dangerouslySetInnerHTML={{ __html: questions[currentQuestion].questionText}}></div>
                </div>
                <div className={styles['answer-section']}>
                  {shuffleQuery(questions[currentQuestion]['answerOptions']).map((answerOption) => (
                    <button onClick={() => handleAnswerOptionClick(answerOption.isCorrect)} key={answerOption.answerText} dangerouslySetInnerHTML={{ __html: answerOption.answerText}}></button>
                  ))}
                </div>
              </>
            )}
          </div>
          : 
          <>
            <div>Loading Questions</div>
          </>
        }
      </div>
    </>
	);
}
export default Quiz;
